<template>
  <div class="container">
    <div class="row mt-4">
      <div class="col-12">
          <form @submit.prevent="submit">
              <h5> Generate API-Key </h5>
              <alert-fail ref="alertFail"> {{ failMessage }} </alert-fail>
              <alert-info ref="alertInfo">  {{ infoMessage }} </alert-info>
              <div class="form-group row">

                <label class="col-sm-2 col-form-label">Username:</label>
                <div class="col-sm-10">
                  <b-form-input
                  ref="user"
                  :state="fieldError.user == null ? null: fieldError.user.isCorrect"
                  size="sm"
                  v-model="user"
                  placeholder="Required">
                </b-form-input>
                <b-form-invalid-feedback class="text-left" :state="false">
                  {{ fieldError.user == null ? null : fieldError.user.message }}
                </b-form-invalid-feedback>
                </div>
              </div>
              <div class="form-group row">
                <label class="col-sm-2 col-form-label">App ID:</label>
                <div class="col-sm-10">
                <b-form-input
                  :state="fieldError.appId == null ? null: fieldError.appId.isCorrect"
                  size="sm"
                  v-model="appId"
                  placeholder="Required">
                </b-form-input>
                <b-form-invalid-feedback class="text-left" :state="false">
                  {{ fieldError.appId == null ? null : fieldError.appId.message }}
                </b-form-invalid-feedback>
                </div>
              </div>
              <div class="row justify-content-center">
                    <button type="button" class="btn btn-sm btn-outline-secondary" @click.prevent="goPreviousPage"  style="margin-right: 10px">Back</button>
                    <button type="submit" class="btn btn-sm btn-outline-primary" :disabled="!isActive"> Generate </button>
              </div>
          </form>

      </div>
    </div>
  </div>
</template>

<script>
import AlertFail from '@/components/AlertFail.vue';
import AlertInfo from '@/components/AlertInfo.vue';

export default {
  data () {
    return {
      infoMessage: 'Info message',
      failMessage: '',
      user: '',
      appId: '',
      isActive: true,
      fieldError: {}
    };
  },
  methods: {
    goPreviousPage () {
      this.$router.push({ name: 'api-key' });
    },
    showSuccess () {
      this.$refs.alertSuccess.countDownChanged(3);
    },
    showFail () {
      this.$refs.alertFail.show();
    },
    hideFail () {
      this.$refs.alertFail.hide();
    },
    showInfo () {
      this.$refs.alertInfo.show();
    },
    submit () {
      this.isActive = false;
      const url = process.env.VUE_APP_SERVICE_HOST + '/api-key/generate-api-key';
      this.axios.post(url, { appId: this.appId, user: this.user })
        .then(response => {
          this.infoMessage = `Success to generate api-key, 
          Username : "${response.data[0].username}" API-Key : "${response.data[0].apiKey}"`;
          this.$refs.alertInfo.show();
        })
        .catch(error => {
          this.isActive = true;
          if (error.response) {
            const data = error.response.data;
            this.failMessage = data.topic;
            this.setFieldError(data.fields);
          } else {
            this.failMessage = error.message;
          }
          this.showFail();
        });
    },
    setFieldError (fields) {
      if (fields) {
        for (const field of fields) {
          this.fieldError[field.param] = { message: field.msg, isCorrect: false };
        }
      }
    }
  },
  components: {
    AlertFail,
    AlertInfo
  },
  mounted () {
    this.$refs.user.focus();
  }
};
</script>

<style scoped>
  form{
        margin-top: 20px;
        font-size: 0.8em;
        color: #333333;
    }
</style>
